@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Raleway:300|Muli&display=swap);
html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }
  .App .landing {
    margin-top: 200px; }

.searchForm {
  padding: 40px 30px 0 30px;
  display: flex;
  justify-content: center;
  background-color: inherit;
  -webkit-transition: margin-top 0.1s;
  transition: margin-top 0.1s; }
  .searchForm .searchInput {
    font-family: 'Muli', sans-serif;
    line-height: 1.2;
    background-color: inherit;
    color: white;
    outline: none;
    border: none;
    border-bottom: solid 1px #36daff;
    font-size: 1.5em;
    padding: 5px 8px; }
    .searchForm .searchInput::-webkit-input-placeholder {
      background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::-moz-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput:-ms-input-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::-ms-input-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::placeholder {
      background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
  .searchForm .searchButton {
    background-color: inherit;
    border: 0;
    outline: 0; }
    .searchForm .searchButton .searchIcon {
      width: 25px;
      height: 25px; }

.searchResults {
  background-color: black;
  padding: 20px 50px;
  text-align: left;
  line-height: 2; }
  .searchResults .resultTitle {
    font-size: 2em;
    background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
    background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .searchResults .titleUnderline {
    background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
    background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
    width: 200px;
    height: 5px;
    margin: 5px 0 20px 0; }
  .searchResults .inlineContainer {
    padding: 10px 0; }
    .searchResults .inlineContainer .inlineHeading {
      padding: 5px 10px;
      margin: 0 20px 0 0;
      background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%); }
    .searchResults .inlineContainer .inlineItem {
      padding: 5px 7px;
      cursor: pointer;
      margin: 0 10px;
      border-radius: 2px;
      background: -webkit-gradient(linear, left top, right top, from(#212121), to(#2a2a2a));
      background: linear-gradient(to right, #212121 0%, #2a2a2a 100%); }
  .searchResults .definitionHeading {
    border-bottom: solid 2px pink;
    padding-bottom: 5px; }
  .searchResults .wordType {
    font-size: 0.8em;
    background-color: #1b1b1b;
    font-style: italic;
    padding: 2px 5px;
    color: orange;
    margin-right: 15px; }
  .searchResults .wordDefinition {
    font-weight: 300; }

.errorMessage {
  background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
  background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2em; }

@media (max-width: 425px) {
  .searchForm {
    position: absolute;
    z-index: 3;
    bottom: 30px;
    padding: 0; } }

